<template>
  <div class="fauce-card">
    <div class="card">
      <div class="container mb-2rem">
        <img class="mt-2rem" src="@/assets/img/proximax-white.png" alt="logo" style="width: 63%">
        <h2 class="text-color text-size-vm30">
          <b>Sirius BC Testnet XPX Faucet</b>
        </h2>
        <p
          style="margin: 0;"
          class="text-color text-size-vm16"
        >Faucet Will only send if XPX is ledscthan 100</p>
      </div>
      <div class="input-class">
        <input style="  width: 100%;" type="text" placeholder="Address" v-model="address">
      </div>
      <div class="button-class">
        <button :disabled="isDisabled" class="btn" @click="sendFaucet()">SEND</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      address: null,
      buttonValidate: true
    };
  },
  methods: {
    /**
     * Method send address to faucet
     */
    sendFaucet() {
      
      if (this.address) {
           console.log('send')
        this.buttonValidate = false;
        this.$apiService
          .get(`faucet/GetXpx/${this.address}`)
          .then(response => {
            this.buttonValidate = true;
            this.$notify({
              title: "success!",
              group: "foo",
              text: `${response.data}`,
              type: "success"
            });
          })
          .catch(error => {
            this.buttonValidate = true;
            let msj = null;
            msj = error.response.data.message
              ? error.response.data.message
              : error.response.data;
            this.$notify({
              group: "foo",
              title: "Error!",
              text: `${msj}`,
              type: "error"
            });
          });
      }
    }
  },
  computed: {
    isDisabled: function() {
      return !this.buttonValidate;
    }
  }
};
</script>

